/* Основной стиль шапки */
.header {
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  background-color: #333;
  position: fixed;
  z-index: 1000;
  padding: 10px 0;
}

.logo img{
  width: 70px;
  height: 70px;
}

.nav-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 10px;
}

.nav-list a {
  color: aliceblue;
  text-decoration: none;
}

.qr-generator-button a {
  color: aliceblue;
  text-decoration: none;
}

.nav-list a:hover {
  color: aquamarine;
}

.qr-generator-button a:hover {
  color: aquamarine;
}